<template>
  <div class='color'>
    <el-tabs v-model="activeName">
      <el-tab-pane name="show" label="">
        <el-table :data="tableData">
          <el-table-column type="expand">
            <template #default="o">
              <el-col :span="12">
                <el-form label-width="80px" :model="o.row" :rules="rules" ref="editForm">
                  <el-form-item label="" prop="name">
                    <el-input v-model="o.row.name"></el-input>
                  </el-form-item>
                  <el-form-item label="" prop="value">
                    <el-input v-model="o.row.value" type="color"></el-input>
                  </el-form-item>
                  <el-form-item label="" prop="sort">
                    <el-input-number v-model="o.row.sort" ></el-input-number>
                  </el-form-item>
                  <el-form-item label="" prop="store">
                    <el-input-number v-model="o.row.store" ></el-input-number>
                  </el-form-item>
                  <el-form-item label="">
                    <el-button type="primary" @click="handleEdit(o.row)"></el-button>
                  </el-form-item>
                </el-form>
              </el-col>
            </template>
          </el-table-column>
          <el-table-column label="" prop="name"></el-table-column>
          <el-table-column label="" prop="value">
            <template #default="o">
              <div :style="{width:'30px',height:'30px',background:o.row.value,border:'1px solid black'}"></div>
            </template>
          </el-table-column>
          <el-table-column label="" prop="sort"></el-table-column>
          <el-table-column label="" prop="store"></el-table-column>
          <el-table-column label="">
            <template #default="o">
              <el-button size="mini" type="danger" @click="handleDelete(o.row.id)"></el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-tab-pane>
      <el-tab-pane name="add" label="">
        <el-col :span="12">
        <el-form label-width="80px" :model="form" :rules="rules" ref="form">
          <el-form-item label="" prop="name">
            <el-input v-model="form.name" clearable  placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="value">
            <el-input type="color" v-model="form.value" clearable  placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="sort">
            <el-input-number v-model="form.sort" clearable  placeholder=""></el-input-number>
          </el-form-item>
          <el-form-item label="" prop="store">
            <el-input-number  v-model="form.store" clearable  placeholder=""></el-input-number>
          </el-form-item>
          <el-form-item label="">
            <el-button type="primary" @click="handleAdd"></el-button>
          </el-form-item>
        </el-form>
        </el-col>
        </el-tab-pane> 
      </el-tabs>
  </div>
</template>

<script>
export default {
  name: 'color',
  data(){
    return {
      activeName:'show',
      // 
      tableData:[],
      // 
      url:"/color",
      // 
      form:{
        name:'',
        value:'',
        sort:'',
        store:'',
        gid:this.gid
      },
      editForm:{},
      rules: {
        name:{required:true,message:"",trigger:"blur"},
        value:{required:true,message:"",trigger:"blur"},
        sort:{required:true,message:"",trigger:"blur"},
        store:{required:true,message:"",trigger:"blur"}
        }
    }
  },
  props:{
    gid: {
      type: Number,
      required: true
    }
  },
  components: {},
  created(){},
  mounted(){
    this.fetchData();
  },
  methods: {
    // 
    fetchData(){
      console.log(this.gid)
      this.$http.get(this.url,{params: {gid:this.gid}}).then(res=>{
        if (res.data.code==200){
          this.tableData = res.data.data;
        }
      })
    },
    // 
    handleAdd(){
      this.$refs.form.validate(v=>{
        if (v){
          this.$http.post(this.url,this.form).then(res=>{
            if (res.data.code==200) {
              this.$message.success("");
              this.fetchData();
              // 
              this.$refs.form.resetFields();
              this.$refs.form.value = '';
              this.$refs.form.sort  = '';
              this.$refs.form.store = '';
              // 
              this.activeName = "show";
              //  
            }
          })
        }else {
              this.$message.error('')
        }
      })
    },
    handleDelete(id){
      this.$confirm(",","",{
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
        center: true
      }).then(()=>{
        this.$http.delete(this.url+"/"+id).then(res=>{
          if (res.data.code ==200){
            this.$message.success("");
            // 
            this.fetchData();
          }
        });
      }).catch(() => {
        this.$message({
          type:"info",
          message:" "
        });
      });
    },
    handleEdit(obj){
      console.log(obj);
      this.$refs.editForm.validate(v=>{
        if (v){
          delete obj.create_time;
          delete obj.update_time;
          this.$http.put(this.url+'/'+obj.id,obj).then(res=>{
            if (res.data.code ==200){
              this.$message.success("");
              this.fetchData;
            }else {
              this.$message.error("");
            }
          })
        }else {
          this.$message.error("");
        }
      });
      
    }
  },
  watch: {
     gid() {
      console.log(this.gid);
      this.form.gid = this.gid;
      this.fetchData()
    }
  }
}
</script>
<style lang='scss' scoped>
</style>
