<template>
  <div class="goods">
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{path:'/main'}"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <el-tabs v-model="activeName">
        <el-tab-pane label="" name="show">
          <!--   -->
          <el-form :inline="true">
            <el-form-item label="" label-width="80px">
              <el-input v-model="searchForm.name" placeholder="" clearable></el-input>
            </el-form-item>
            <el-form-item label="" label-width="80px">
              <el-col :span="12">
                <el-input v-model="searchForm.min_price" placeholder="" clearable></el-input>
              </el-col>
              <el-col :span="12">
                <el-input v-model="searchForm.max_price" placeholder="" clearable></el-input>
              </el-col>
            </el-form-item>
            <el-form-item label="" label-width="80px">
              <el-select v-model="searchForm.tid" clearable>
                <el-option
                  v-for="item in typeOption"
                  :key="item.id"
                  :label="item.typename"
                  :value="item.id"
                ></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="" label-width="80px">
              <el-select v-model="searchForm.state" clearable>
                <el-option
                  v-for="item in stateOption"
                  :key="item.id"
                  :label="item.label"
                  :value="item.value"
                ></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="" label-width="80px">
              <el-select v-model="searchForm.recommend" clearable>
                <el-option
                  v-for="item in recomOption"
                  :key="item.id"
                  :label="item.label"
                  :value="item.value"
                ></el-option>
              </el-select>
            </el-form-item>
            <el-button type="primary" icon="el-icon-search" circle @click="handleSearch"></el-button>
          </el-form>
          <!--   -->
          <el-divider></el-divider>
          <!--  -->
          <el-table v-bind:data="tableData" style="width:100%">
            <el-table-column label="id" prop="id" align="center" width="80px" fixed></el-table-column>
            <el-table-column
              label=""
              prop="name"
              align="center"
              :show-overflow-tooltip="true"
              fixed
            ></el-table-column>
            <el-table-column label="" prop="thumb" align="center">
              <template slot-scope="scope">
                <img :src="scope.row.thumb" alt style="height:40px;width:60px" />
              </template>
            </el-table-column>
            <el-table-column label="" prop="price" align="center" width="120px">
              <template slot-scope="scope">{{scope.row.price|currency}}</template>
            </el-table-column>
            <el-table-column label="" prop="special_price" align="center" width="120px">
              <template slot-scope="scope">{{scope.row.special_price|currency}}</template>
            </el-table-column>
            <el-table-column label="" prop="sort" align="center" width="60px"></el-table-column>
            <el-table-column label="" prop="tid" align="center" width="60px">
              <template #default="o">{{o.row.type.typename}}</template>
            </el-table-column>
            <el-table-column label="" prop="state" align="center" width="100px">
              <template slot-scope="scope">{{scope.row.state===1?"":""}}</template>
            </el-table-column>
            <el-table-column label="" prop="recommend" align="center" width="100px">
              <template slot-scope="scope">{{scope.row.recommend===1?"":""}}</template>
            </el-table-column>
            <el-table-column
              label=""
              prop="create_time"
              align="center"
              :show-overflow-tooltip="true"
              max-width="120px"
            ></el-table-column>
            <el-table-column
              label=""
              prop="update_time"
              align="center"
              :show-overflow-tooltip="true"
              max-width="120px"
            ></el-table-column>
            <el-table-column label="" align="center" width="200px">
              <template slot-scope="scope">
                <el-button size="mini" @click="handleEdit(scope.row)"></el-button>
                <el-button size="mini" type="danger" @click="handleDelete(scope.row.id)"></el-button>
              </template>
            </el-table-column>
            <el-table-column label="" align="center">
              <template #default="o">
                <el-button size="mini" @click="handleContentEdit(o.row.id)"></el-button>
              </template>
            </el-table-column>
            <el-table-column label="" align="center">
              <template #default="o">
                <el-button size="mini" @click="handleColorEdit(o.row.id)"></el-button>
              </template>
            </el-table-column>
            <el-table-column label="" align="center">
              <template #default="o">
                <el-switch
                  :value="o.row.state==1"
                  active-color="#13ce66"
                  inactive-color="#ff4949"
                  @click.native="handleStateChange(o.row.id,o.row.state)"
                ></el-switch>
              </template>
            </el-table-column>
          </el-table>
          <el-row type="flex" justify="center" align="middle" style="height:40px">
            <el-pagination
              background
              :current-page.sync="page"
              :page-sizes="[5, 10, 15, 20]"
              :page-size.sync="pageSize"
              layout="total, sizes, prev, pager, next"
              :total="total"
            ></el-pagination>
          </el-row>
        </el-tab-pane>
        <el-tab-pane label="" name="add">
          <el-col :span="12">
            <el-form label-width="80px" :rules="rules" :model="addForm" ref="addForm">
              <el-form-item label="" prop="name">
                <el-input v-model="addForm.name" style="width:300px"></el-input>
              </el-form-item>
              <el-form-item label="" prop="thumb">
                <!--   -->
                <el-upload
                  class="avatar-uploader"
                  action="api/admin/upload"
                  :show-file-list="false"
                  :on-success="handleAvatarSuccess"
                  :before-upload="beforeAvatarUpload"
                >
                  <img v-if="addForm.thumb" :src="addForm.thumb" class="avatar" />
                  <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                </el-upload>
              </el-form-item>
              <el-form-item label="" prop="price">
                <el-input-number v-model="addForm.price" style="width:300px"></el-input-number>
              </el-form-item>
              <el-form-item label="" prop="special_price">
                <el-input-number v-model="addForm.special_price" style="width:300px"></el-input-number>
              </el-form-item>
              <el-form-item label="" prop="sort">
                <el-input-number v-model="addForm.sort" style="width:300px"></el-input-number>
              </el-form-item>
              <el-form-item label="" prop="tid">
                <!-- <el-autocomplete
                  class="inline-input"
                  v-model="typename"
                  :fetch-suggestions="querySearch"
                  placeholder=""
                  :trigger-on-focus="false"
                  @select="handleSelect"
                ></el-autocomplete>-->
                <el-select v-model="addForm.tid">
                  <el-option
                    v-for="item in typeOption"
                    :key="item.id"
                    :label="item.typename"
                    :value="item.id"
                  ></el-option>
                </el-select>
              </el-form-item>
              <!-- <el-form-item label="" prop="state">
                <el-radio-group v-model="addForm.state">
                  <el-radio-button label="1"></el-radio-button>
                  <el-radio-button label="0"></el-radio-button>
                </el-radio-group>
              </el-form-item>-->
              <el-form-item label="" prop="recommend">
                <el-radio-group v-model="addForm.recommend">
                  <el-radio-button label="1"></el-radio-button>
                  <el-radio-button label="0"></el-radio-button>
                </el-radio-group>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="handleAdd"></el-button>
              </el-form-item>
            </el-form>
          </el-col>
        </el-tab-pane>
      </el-tabs>
    </el-card>
    <el-dialog title="" :visible.sync="dialogVisible" width="50%">
      <el-form label-width="80px" :rules="rules" :model="editForm" ref="editForm">
        <el-form-item label="" prop="name">
          <el-input v-model="editForm.name" style="width:450px"></el-input>
        </el-form-item>
        <el-form-item label="" prop="thumb">
          <!--   -->
          <el-upload
            class="avatar-uploader"
            action="api/admin/upload"
            :show-file-list="false"
            :on-success="handleAvatarSuccessEdit"
            :before-upload="beforeAvatarUpload"
          >
            <img v-if="editForm.thumb" :src="editForm.thumb" class="avatar" />
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
        </el-form-item>
        <el-form-item label="" prop="price">
          <el-input-number v-model="editForm.price" style="width:450px"></el-input-number>
        </el-form-item>
        <el-form-item label="" prop="special_price">
          <el-input-number v-model="editForm.special_price" style="width:450px"></el-input-number>
        </el-form-item>
        <el-form-item label="" prop="sort">
          <el-input-number v-model="editForm.sort" style="width:450px"></el-input-number>
        </el-form-item>
        <el-form-item label="" prop="tid">
          <el-autocomplete
            class="inline-input"
            v-model="editName"
            :fetch-suggestions="querySearch"
            placeholder=""
            :trigger-on-focus="false"
            @select="handleSelectEdit"
          ></el-autocomplete>
        </el-form-item>
        <el-form-item label="" prop="state">
          <el-radio-group v-model="editForm.state" prop="state">
            <el-radio-button label="1"></el-radio-button>
            <el-radio-button label="0"></el-radio-button>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="" prop="recommend">
          <el-radio-group v-model="editForm.recommend" prop="recommend">
            <el-radio-button label="1"></el-radio-button>
            <el-radio-button label="0"></el-radio-button>
          </el-radio-group>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible=false"></el-button>
        <el-button type="primary" @click="handleUpdate()"></el-button>
      </span>
    </el-dialog>
    <el-dialog title="" :visible.sync="dialogVisible2" width="70%">
      <goods-content :gid="gid" @success="dialogVisible2=false"></goods-content>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible2=false"></el-button>
        <!-- <el-button type="primary" @click="handleUpdate()"></el-button> -->
      </span>
    </el-dialog>
    <el-dialog title="" :visible.sync="dialogVisible3" width="70%">
      <goods-color :gid="gid" @success="dialogVisible3=false"></goods-color>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible3=false"></el-button>
        <!-- <el-button type="primary" @click="handleUpdate()"></el-button> -->
      </span>
    </el-dialog>
  </div>
</template>

<script>
//  
import GoodsEdit from "@/components/GoodsContent.vue";
import ColorEdit from "@/components/GoodsColor.vue";
export default {
  name: "goods",
  data() {
    return {
      // 
      activeName: "show",
      // 
      tableData: [],
      // search 
      searchForm: {
        name: "",
        min_price: "",
        max_price: "",
        tid: "",
        state: "",
        recommend: ""
      },
      //
      typeOption: [],
      stateOption: [
        { label: "", value: "0" },
        { label: "", value: "1" }
      ],
      recomOption: [
        { label: "", value: "0" },
        { label: "", value: "1" }
      ],
      // 
      url: "/goods",
      // 
      total: 0,
      // 
      page: 1,
      // 
      pageSize: 5,
      // 
      typename: "",
      // 
      editName: "",
      // 
      dialogVisible: false,

      dialogVisible2: false,

      dialogVisible3: false,
      // 
      addForm: {
        name: "",
        thumb: "",
        price: "",
        special_price: "",
        sort: "",
        tid: "",
        state: "0",
        recommend: "0"
      },
      // 
      editForm: {},
      rules: {
        name: { required: true, message: "", trigger: "blur" },
        thumb: { required: true, message: "" },
        price: { required: true, message: "", trigger: "blur" },
        special_price: {
          required: true,
          message: "",
          trigger: "blur"
        },
        sort: { required: true, message: "", trigger: "blur" },
        tid: [
          { required: true, message: "", trigger: "blur" },
          {
            type: "number",
            message: "",
            trigger: "blur"
          }
        ],
        // state:{required: true, message:""},
        recommend: { required: true, message: "" }
      },
      // 
      gid: 0
    };
  },
  components: {
    // 
    "goods-content": GoodsEdit,
    "goods-color": ColorEdit
  },
  created() {},
  mounted() {
    // 
    this.fetchData();
    this.fetchTypeDate();
  },
  methods: {
    //  
    fetchData() {
      let params = {
        page: this.page,
        pageSize: this.pageSize
      };
      if (this.searchForm.name) {
        params.name = this.searchForm.name;
      }
      if (this.searchForm.min_price && this.searchForm.max_price) {
        params.min_price = this.searchForm.min_price;
        params.max_price = this.searchForm.max_price;
      }
      if (this.searchForm.tid) {
        params.tid = this.searchForm.tid;
      }
      if (this.searchForm.state) {
        params.state = this.searchForm.state;
      }
      if (this.searchForm.recommend) {
        params.recommend = this.searchForm.recommend;
      }
      this.$http
        .get(this.url, {
          params
          // get  params
        })
        .then(res => {
          if (res.data.code == 200) {
            this.tableData = res.data.data;
            this.total = res.data.total;
            // this.typename = res.data.tid;
          } else {
            this.$message.error("");
          }
        });
    },
    // 
    fetchTypeDate() {
      this.$http
        .get("/type", {
          params: {
            level: 2
          }
        })
        .then(res => {
          if (res.data.code == 200) {
            this.typeOption = res.data.data;
          }
        });
    },
    // 
    handleDelete(id) {
      this.$confirm(",", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
        center: true
      })
        .then(() => {
          this.$http.delete(this.url + "/" + id).then(res => {
            if (res.data.code == 200) {
              this.$message.success("");
              this.fetchData();
            }
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: " "
          });
        });
    },
    // 
    handleAvatarSuccess(res, file) {
      // this.imageUrl = URL.createObjectURL(file.raw);
      this.addForm.thumb = res;
      this.$refs.addForm.validateField("thumb");
    },
    // 
    handleAvatarSuccessEdit(res, file) {
      // this.imageUrl = URL.createObjectURL(file.raw);
      this.editForm.thumb = res;
      this.$refs.editForm.validateField("thumb");
    },
    // 
    beforeAvatarUpload(file) {
      // const isJPG = file.type === "image/jpeg";
      const isJPG =
        ["image/jpeg", "image/jpg", "image/png"].indexOf(file.type) !== -1;
      const isLt2M = file.size / 1024 / 1024 < 2;
      if (!isJPG) {
        this.$message.error(" JPG JPEG PNG !");
      }
      if (!isLt2M) {
        this.$message.error(" 2MB!");
      }
      return isJPG && isLt2M;
    },
    // 
    handleAdd() {
      this.$refs.addForm.validate(v => {
        if (v) {
          this.$http.post(this.url, this.addForm).then(res => {
            if (res.data.code == 200) {
              this.$message.success("");
              this.$refs.addForm.resetFields();
              this.activeName = "show";
              this.fetchData();
            } else {
              this.$message.error("");
            }
          });
        }
      });
    },
    // 
    querySearch(queryString, cb) {
      this.$http
        .get("/type", {
          params: {
            page: 1,
            pageSize: 100,
            name: queryString
          }
        })
        .then(res => {
          let type = res.data.data;
          let options = type.map(v => ({
            value: v.typename,
            id: v.id
          }));
          cb(options);
        });
    },
    // id
    handleSelectshow(res) {
      this.addForm.tid = res.id;
      this.$refs.addForm.validateField("tid");
    },
    // id
    handleSelect(res) {
      this.addForm.tid = res.id;
      this.$refs.addForm.validateField("tid");
    },
    // id
    handleSelectEdit(res) {
      this.editForm.tid = res.id;
      this.$refs.editForm.validateField("tid");
    },
    //  
    handleEdit(obj) {
      this.editForm = obj;
      this.editForm.state = String(this.editForm.state);
      this.editForm.recommend = String(this.editForm.recommend);
      this.$http.get("/type/" + obj.tid).then(res => {
        if (res.data.code == 200) {
          this.editName = res.data.data.typename;
        } else {
          this.$message.error("");
        }
      });
      this.dialogVisible = true;
    },
    // 
    handleUpdate() {
      this.$refs.editForm.validate(v => {
        if (v) {
          delete this.editForm.create_time;
          delete this.editForm.update_time;
          this.$http
            .put(this.url + "/" + this.editForm.id, this.editForm)
            .then(res => {
              if (res.data.code == 200) {
                this.$message.success("");
                this.dialogVisible = false;
                this.fetchData;
              } else {
                this.$message.error("");
              }
            });
        } else {
          this.$message.error("");
        }
      });
    },
    //  
    handleSearch() {
      this.fetchData();
    },
    // 
    handleContentEdit(id) {
      this.dialogVisible2 = true;
      this.gid = id;
    },
    handleColorEdit(id) {
      this.dialogVisible3 = true;
      this.gid = id;
    },
    handleStateChange(id, state) {
      if (state == 1) {
        this.$http.put(this.url + "/" + id, { state: 0 }).then(res => {
          if (res.data.code == 200) {
            this.$message.success("");
            this.fetchData();
          }
        });
      } else {
        let p1 = this.$http.get("/detail", { params: { gid: id } });
        let p2 = this.$http.get("/color", { params: { gid: id } });
        Promise.all([p1, p2]).then(res => {
          if (res[0].data.data.length > 0 && res[1].data.data.length > 0) {
            this.$http.put(this.url + "/" + id, { state: 1 }).then(res => {
              if (res.data.code === 200) {
                this.$message.success("");
                this.fetchData();
              }
            });
          } else {
            this.$message.error("");
          }
        });
      }
    }
  },
  // 
  filters: {
    showPrice(price) {
      return "" + this.price.toFixed(2);
    }
  },
  watch: {
    // 
    page(newValue, oldValue) {
      this.fetchData();
    },
    // 
    pageSize(newValue, oldValue) {
      this.fetchData();
    }
  }
};
</script>
<style lang='scss' scoped>
.el-breadcrumb {
  line-height: 60px;
}
.el-card {
  margin-top: 20px;
  margin-left: 20px;
  // height: 450px;
}
.avatar-uploader .el-upload {
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
  border: 1px dashed #d9d9d9;
}
.avatar {
  width: 178px;
  height: 178px;
  display: block;
}
.el-table {
  margin-top: 30px;
}
.el-form {
  margin-top: 30px;
}
</style>
