<template>
  <div class>
    <!--   -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <!--  -->
      <el-tabs v-model="activeName">
        <!--  -->
        <el-tab-pane label="" name="show">
          <el-table :data="tableData">
            <el-table-column label="id" prop="id" align="center"></el-table-column>
            <el-table-column label="" prop="thumb" align="center">
              <template slot-scope="scope">
                <img :src="scope.row.thumb" alt style="height:40px;width:60px" />
              </template>
            </el-table-column>
            <el-table-column label="" prop="create_time" align="center"></el-table-column>
            <el-table-column label="" prop="update_time" align="center"></el-table-column>
            <el-table-column label="" prop="status" align="center">
              <template #default="o">{{o.row.status===1?"":""}}</template>
            </el-table-column>
            <el-table-column label="" prop="sort" align="center"></el-table-column>
            <el-table-column label="" align="center">
              <template #default="o">
                <el-button type="text" @click="handleEdit(o.row)"></el-button>
                <el-button type="text" @click="handleDelete(o.row.id)"></el-button>
              </template>
            </el-table-column>
          </el-table>
          <el-row type="flex" justify="center" align="middle" style="height:40px">
            <el-pagination
              background
              :current-page.sync="page"
              :page-sizes="[5, 10, 15, 20]"
              :page-size.sync="pageSize"
              layout="total, sizes, prev, pager, next"
              :total="total"
            ></el-pagination>
          </el-row>
          <!--  -->
        </el-tab-pane>
        <!--  -->
        <el-tab-pane label="" name="add">
          <el-col :span="12">
            <el-form label-width="80px" :model="addForm" :rules="rules" ref="addForm">
              <el-form-item label="" prop="thumb">
                <!--   -->
                <el-upload
                  class="avatar-uploader"
                  action="api/admin/upload"
                  :show-file-list="false"
                  :on-success="handleAvatarSuccess"
                  :before-upload="beforeAvatarUpload"
                >
                  <img v-if="addForm.thumb" :src="addForm.thumb" class="avatar" />
                  <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                </el-upload>
              </el-form-item>
              <el-form-item label="" prop="gid">
                <el-autocomplete
                  class="inline-input"
                  v-model="name"
                  :fetch-suggestions="querySearch"
                  placeholder=""
                  :trigger-on-focus="false"
                  @select="handleSelect"
                ></el-autocomplete>
              </el-form-item>
              <el-form-item label="" prop="status">
                <el-radio v-model="addForm.status" label="1" style="display:block"></el-radio>
                <el-radio v-model="addForm.status" label="0"></el-radio>
              </el-form-item>
              <el-form-item label="" prop="sort">
                <el-input-number v-model="addForm.sort"></el-input-number>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="handleAdd"></el-button>
              </el-form-item>
            </el-form>
          </el-col>
        </el-tab-pane>
      </el-tabs>
    </el-card>
    <el-dialog
  title=""
  :visible.sync="dialogVisible"
  width="70%">
  <el-form label-width="80px" :model="editForm" :rules="rules" ref="editForm">
              <el-form-item label="" prop="thumb">
                <!--   -->
                <el-upload
                  class="avatar-uploader"
                  action="api/admin/upload"
                  :show-file-list="false"
                  :on-success="handleAvatarSuccess1"
                  :before-upload="beforeAvatarUpload"
                >
                  <img v-if="editForm.thumb" :src="editForm.thumb" class="avatar" />
                  <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                </el-upload>
              </el-form-item>
              <el-form-item label="" prop="gid">
                <el-autocomplete
                  class="inline-input"
                  v-model="editname"
                  :fetch-suggestions="querySearch"
                  placeholder=""
                  :trigger-on-focus="false"
                  @select="handleSelect1"
                ></el-autocomplete>
              </el-form-item>
              <el-form-item label="" prop="status">
                <el-radio v-model="editForm.status" label="1"></el-radio>
                <el-radio v-model="editForm.status" label="0"></el-radio>
              </el-form-item>
              <el-form-item label="" prop="sort">
                <el-input-number  v-model="editForm.sort"></el-input-number>
              </el-form-item>
            </el-form>
  <span slot="footer" class="dialog-footer">
    <el-button @click="dialogVisible = false"> </el-button>
    <el-button type="primary" @click="handleUpdate()"> </el-button>
  </span>
</el-dialog>
  </div>
</template>

<script>
export default {
  name: "banner",
  data() {
    return {
      // 
      activeName: "show",
      // 
      tableData: [],
      // 
      url: "/banner",
      // 
      name: "",
      // 
      editname:"",
      //  
      total: 0,
      // 
      pageSize: 5,
      // 
      page: 1,
      thumb: "",
      imageUrl: "",
      // 
      addForm: {
        thumb: "",
        gid: "",
        status: "0",
        sort: ""
      },
      rules: {
        thumb: { required: true, message: "" },
        gid: [
          { required: true, message: "", trigger: "blur" },
          {
            type: "number",
            message: "",
            trigger: "blur"
          }
        ],
        status: { required: true, message: "" },
        sort: { required: true, message: "", trigger: "blur" }
      },
      editForm:{ },
      // 
      dialogVisible:false,
    };
  },
  mounted() {
    // 
    this.fetchData();
  },
  methods: {
    //  
    fetchData() {
      this.$http
        .get(this.url, {
          params: {
            // getparams data
            page: this.page,
            pageSize: this.pageSize
          }
        })
        .then(res => {
          if (res.data.code == 200) {
            this.tableData = res.data.data;
            this.total = res.data.total;
          } else {
            this.$message.error("");
          }
        });
    },
    // 
    handleAvatarSuccess(res, file) {
      // this.imageUrl = URL.createObjectURL(file.raw);
      this.addForm.thumb = res;
      this.$refs.addForm.validateField("thumb");
    },
    // 
    handleAvatarSuccess1(res, file) {
      // this.imageUrl = URL.createObjectURL(file.raw);
      this.editForm.thumb = res;
      this.$refs.editForm.validateField("thumb");
    },
    // 
    beforeAvatarUpload(file) {
      const isJPG = file.type === "image/jpeg";
      // const isPNG = file.type === "png";
      const isLt2M = file.size / 1024 / 1024 < 2;
      if (!isJPG) {
        this.$message.error(" JPG !");
      }
      if (!isLt2M) {
        this.$message.error(" 2MB!");
      }
      return isJPG && isLt2M;
    },
    // 
    querySearch(queryString, cb) {
      this.$http
        .get("/goods", {
          params: {
            page: 1,
            pageSize: 100,
            name: queryString
          }
        })
        .then(res => {
          let goods = res.data.data;
          let options = goods.map(v => ({
            value: v.name,
            id: v.id
          }));
          cb(options);
        });
    },
    // id
    handleSelect(res) {
      this.addForm.gid = res.id;
      this.$refs.addForm.validateField("gid");
    },
    // id
    handleSelect1(res) {
      this.editForm.gid = res.id;
      this.$refs.editForm.validateField("gid");
    },
    // 
    handleAdd() {
      this.$refs.addForm.validate(v => {
        if (v) {
          this.$http.post(this.url, this.addForm).then(res => {
            if (res.data.code == 200) {
              this.$message.success("");
              this.$refs.addForm.resetFields();
              this.activeName = "show";
              this.fetchData();
            } else {
              this.$message.error("");
            }
          });
        }
      });
    },
    // 
    handleDelete(id) {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
        center: true
      })
        .then(() => {
          this.$http.delete(this.url + "/" + id).then(res => {
            if (res.data.code === 200) {
              this.$message.success("");
              this.fetchData();
            }
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    },
    //   
    handleEdit(obj){
      this.editForm = obj;
      this.editForm.status = String(this.editForm.status);
      this.$http.get("/goods/"+obj.gid).then(res=>{
        if (res.data.code==200){
          this.editname = res.data.data.name;
        }else {
          this.$message.error("");
        }
      })
      this.dialogVisible = true;
    },
    //  
    handleUpdate(){
      this.$refs.editForm.validate(v=>{
        if (v){
          delete this.editForm.create_time;
          delete this.editForm.update_time;
          this.$http.put(this.url+"/"+this.editForm.id,this.editForm).then(res=>{
            if (res.data.code==200){
              this.$message.success("");
              this.dialogVisible = false;
              this.fetchData();
            }
          })
        }else {
          this.$message.error("");
        }
      })
    }
  },
  watch: {
    // 
    page(newValue, oldValue) {
      this.fetchData();
    },
    // 
    pageSize(newValue, oldValue) {
      this.fetchData();
    }
  }
};
</script>
<style lang='scss' scoped>
.el-breadcrumb {
  line-height: 60px;
}
.el-card {
  margin-top: 20px;
  margin-left: 20px;
  // height: 450px;
}
.avatar-uploader .el-upload {
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
  border: 1px dashed #d9d9d9;
}
.avatar {
  width: 178px;
  height: 178px;
  display: block;
}
.el-table{
  margin-top: 30px;
}
.el-form{
  margin-top: 30px;
}
</style>
